import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const Users = () => import("../views/Users.vue")
const UsersDetail = () => import("../views/UsersDetail.vue")
const UsersEdit = () => import("../views/UsersEdit.vue")
const Playerm3u8 = () => import("../components/Playerm3u8.vue")
const routes = [
  {
    path: "/",
    name: "Home",
    component: Home
  },
  {
    path: "/about",
    name: "About",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/About.vue")
  },
  {
    path: "/video",
    name: "video",
    component: Playerm3u8
  },
  {
    // path: "/users/:userId",
    path: "/users",
    name: "Users",
    
    // beforeEnter:(to, from, next) => {
    //   console.log('to : ', to, ' \n from :', from, ' next : ', next)
    //   console.log('beforeEnter')
    //   next()
    //   // next({name:'About'})
      
    // },
    component: Users,
    children: [
      {
        path:":id"
        , name: "users-detail"
        , component: UsersDetail
      },
      {
        path:":id/edit"
        , name: "users-edit"
        , component: UsersEdit
      }
    ]

  },  
  {
    path: "/youtube",
    name: "youtuebe",
    component: () => import("../views/Youtube.vue")
  },
  {
    path: '/redirect-me',
    redirect:{ name :'Users'}
  },
  {
    // 위에중에 아무것도 걸리지 않으면 여기서 작업
    path:'/*',
    redirect:{ name : 'Home'}

  }
  // {
  //   path:'*',
  //   name:'*',
  //   component:() => import("../views/notfound.vue")

  // }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

export default router;
